<script>
import { ChecksSelectedData } from './../../../data.checks_selected.js';
import { page } from './../../../turpial.route.store.js';
import { RouterStore } from './../../../RouterStore.js';
import { ApplicationData } from './../../../data.application.route.js';
import { turpial } from '../../../libraries/turpial.js';
import AppForms from '../../../libraries/coreApp/AppForms.js';
import { ButtonStatus } from '../../../libraries/coreApp.js';

let orders_selected = $ChecksSelectedData.checks_selected;
let has_multiple = false;
if(orders_selected.length > 0){
    has_multiple = true;
}

$: {
    if(($RouterStore.parameters.order === "multiple" && $ChecksSelectedData.checks_selected.length === 0)){
        page.route('projects/orders');
    }
}

$: {
    if(applications.length == 0){
        page.route('projects/applications');
    }
}


function setCardActive(e){
    if(e.target.matches('.card-process-item')){
        clearActiveClass(e);

        let cardSelected = e.target;
        cardSelected.classList.add('active');
    }
}

function clearActiveClass(e){
    let cards = [...e.currentTarget.querySelectorAll('.card-process-item')];
    cards.map((card) => {
        card.classList.remove('active');
    });
}

let form = null;
let id_label = turpial.random_string(8);
let submitButton = null;
let order_id = $RouterStore.parameters.order;

function send_order(form, button){
    let data = AppForms.retrieve({
        form: form,
        targets: ".formElements",
    });

    let items = [];
    if(order_id !== "multiple"){
        items = [order_id];
    }else{
        items = $ChecksSelectedData.checks_selected
    }

    ButtonStatus(submitButton, "disabled");

    turpial.fetch({
        url: [`${$ApplicationData.api_host}public/api/v1/services/builder/orders/send_order/${order_id}/${data.application}orders=${items.join(",")}`],
        type: "text",
        method: "GET",
        options: {
            'Content-Type': "text/plain",
            'X-Token-Website': $ApplicationData.token_website,
            'X-User-Token': $ApplicationData.user.public_key
        },
        ready: (data)=>{
            setTimeout(() => {
                ButtonStatus(submitButton, "enabled");
                page.route(`projects/orders/${order_id}`)
            }, 400); 
        }
    })
}


let applications = [];
$: {
    if( ($ApplicationData?.website_data?.installed_apps ?? null) !== null ){
        applications = $ApplicationData?.website_data?.installed_apps;
        let fill_object_apps = [];
        applications = Object.values(applications).map(function( app, key ){
            let name = Object.keys(applications)[key];
            if((app?.installed ?? false) === true){
                let installed = $ApplicationData.app_store.find(function( app_from_store ){
                    if(app_from_store.brand_id === name){
                        return app_from_store;
                    }
                });
                if(installed){
                    fill_object_apps.push(installed)
                }
            }
            
        })

        applications = fill_object_apps;
    }
}



</script>


<section>
    <form action="" bind:this="{form}" method="POST" on:submit|preventDefault="{()=>{ send_order(form) }}">
        <div class="header-process has-text-gray">
            <a href="#path:/{$ChecksSelectedData.from_path}/" class="flex-center-flex-start icon-back">
                <i class="icon-tripod-arrow-back is-size-2"></i>
                <p style="margin-left: 1rem;">Volver</p>
            </a>
            <div class="title-process has-text-centered fw600">
                {#if has_multiple}
                    Procesar ordenes
                    {:else}
                        Procesar orden
                {/if}
                
            </div>
            <div class="sub-title-process has-text-centered mt-3">
                Selecciona la aplicación a la cual vas a enviar la orden
                {#if has_multiple}
                    las ordenes
                    {:else}
                        la orden
                {/if}
            </div>
        </div>
        
        <div class="columns is-multiline is-centered cards-container mt-6" on:click={(e) => setCardActive(e)}>
            {#each applications as app}
                <label>
                    <div class="card-process-item has-shadow column is-2">
                        <img src="{app.logos[0]}" alt="{app.name+"-logo"}">                    
                    </div>
                    <input class="formElements" type="radio" name="application" value={app.brand_id} required>
                </label>
            {/each}
        </div>
        
        <div class="buttons is-centered is-grouped mt-6">
            <button class="button brand is-primary is-rounded btn-process" bind:this="{submitButton}">Enviar</button>
        </div>
    </form>
</section>

<style>
input[type='radio']{
    opacity: 0;
    position: absolute;
}
.has-text-gray{
    color: #415B76;
}

.title-process{
    font-size: 1.75rem;
}
.cards-container{
    display: flex;
    position: relative;
    gap: 41px;
}
.cards-container div{
    width: 190px;
    min-height: 275px;
    border-radius: 20px;
    cursor: pointer;
    display: grid;
    place-items: center;
}

.cards-container div img{
    width: 70%;
    height: auto;
    pointer-events: none;
    user-select: none;
}

.cards-container div:global(.active){
    border: 2px solid #296FFF;
}


.icon-back{
    text-decoration: none;
    cursor: pointer;
    color: #415B76;
    font-weight: 600;
}
</style>