<script>
	import { InlineRoute } from './../../inline.routes.js';
import ChartistGraphic from './../../components/charts/ChartistGraphic.svelte';
import IndexHeaderBox from './IndexHeaderBox.svelte';
import { ApplicationData } from '../../data.application.route';
import { CustomStore } from '../../libraries/customStore/CustomStore';
import LinkPrimary from './../../components/Buttons/LinkPrimary.svelte';
import Graphic from '../../components/charts/Graphic.svelte';
import BannerApplications from '../../components/Elements/BannerApplications.svelte';
let isData = true;

let dashboardData = new CustomStore(
    {
        order_counter: 0,
        order_value: 0,
        form_counter: 0,
        form_value: 0,
        from: "",
        until: "",
        orders: {
            completeData: {},
            labels: [],
            data: []
        },
        forms: {
            completeData: {},
            labels: [],
            data: []
        }
    },
    function(){
        dashboardData = dashboardData;
    }
);

// paginatorData.update((object)=>{                
//                 object.conditions.offset = offset;
//                 return object; 
//             })

let show_charts = false;
let data_charts = new CustomStore({}, ()=>{
    data_charts = data_charts;
})

function fetch_chart(){
    let url = `${$ApplicationData.api_host}public/api/v1/services/user/dashboard_chart`;

    if(dashboardData.data.from !== "" && dashboardData.data.until !== ""){
        url = url+`?from=${dashboardData.data.from}&until=${dashboardData.data.until}`
    }

    fetch(url, {
        headers: {'X-Token-Website': $ApplicationData.token_website},
    }).then(r => r.json()).then((r)=>{
        data_charts.update(function(data){
            data = r
            return data;
        })
        show_charts = true;
    })
}

function dashboard_fetch_all() {
    
    
    fetch_chart();

    fetch(`${$ApplicationData.api_host}public/api/v1/services/builder/orders/dashboard?search=&from=${dashboardData.data.from}&until=${dashboardData.data.until}`, {
        headers: {'X-Token-Website': $ApplicationData.token_website},
    }).then(r => r.json()).then((r)=>{    
        console.log(r)
        dashboardData.setJson("order_value", r.sum);
        dashboardData.setJson("order_counter", r.count);
        dashboardData.update((object)=>{
            object.orders.completeData = r.data;
            return object;
        })
    })
}

dashboard_fetch_all();




</script>
{#if $ApplicationData.has_projects === true}
    <section>
        <IndexHeaderBox dashboardData={dashboardData} dashboard_fetch_all={dashboard_fetch_all}></IndexHeaderBox>
        <div class="columns is-multiline pb-6">
            <div class="column is-6">
                <div class="card has-background-lighter-grey box is-align-items-center is-justify-content-center is-flex" style="height: 100%;">
                    <div>
                        <p class="has-text-centered fw600 font-size-14 has-text-light-grey">
                            <span>
                                Ventas
                            </span>
                            <br>
                            <span class="is-size-2">
                                {`$${dashboardData?.data?.order_value}`}
                            </span>
                            <br>
                            {dashboardData?.data?.order_counter} ordenes
                        </p>
                    </div>
                </div>
            </div>
            <div class="graphics-container flex-center column is-6">
                <div style="width:100%;">
                    {#if show_charts}
                        <div class="sales-chart">
                            <ChartistGraphic data_charts={data_charts} type="sales-value" chartId="chart1" chartTittle="Ingresos"></ChartistGraphic>
                        </div>
                    {/if}
                </div>
            </div>
        </div>
    </section>
    <section>
        <BannerApplications />
    </section>
{/if}
